import React from "react";
import styled from 'styled-components';
import { GithubButton, LinkedinButton } from "./SocialButtons";
const SocialLinksContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 32px 0px;
`

const SocialLinks = () => {
  return (
    <SocialLinksContainer>
      <GithubButton url={'https://github.com/chc94818'}></GithubButton>
      <LinkedinButton url={'https://www.linkedin.com/in/%E5%AE%B6%E8%B1%AA-%E5%BC%B5-572a0317b/'}></LinkedinButton>
    </SocialLinksContainer>
  )
}

export default SocialLinks;
