import React from "react";
import styled from 'styled-components';
import Section from './Section';
import Card from './Card';
import projectData from '../../data/projectData.json';

const projectList = projectData.projectList

const SectionProjectContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 64px;
  padding: 0 14% 0;
  align-items: center;
`;

const SectionProjectTitle = styled.h2`
  font-size: 3em;
`;

const StyledCard = styled(Card)`
  margin: 0 0 48px;
`;

const projectCardList = projectList.map((project, index) => {
  const { title, content } = project;
  return (
    <StyledCard key={index} title={title} content={content}></StyledCard>
  )
})

const SectionProject = ({id}) => {
  return (
    <Section id={id}>
      <SectionProjectContainer>
        <SectionProjectTitle>{id.toUpperCase()}</SectionProjectTitle>
        {projectCardList}
      </SectionProjectContainer>
    </Section>
  )
}


export default SectionProject;