import React from "react";
import styled from 'styled-components';
import Section from './Section';
import Card from './Card';
import Timeline from '../Timeline/Timeline';
import experienceData from '../../data/experienceData.json';

const experienceList = experienceData.experienceList

const SectionExperienceContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 64px;
  padding: 0 14% 0;
  align-items: center;
`;

const SectionExperienceTitle = styled.h2`
  font-size: 3em;
`;

const StyledCard = styled(Card)`
  margin: 0 0 48px;
`;


// const ExperienceCardList = experienceList.map((exp, index) => {
//   const { title, content } = exp;
//   return (
//     <StyledCard key={index} title={title} content={content}></StyledCard>
//   )
// })

const SectionExperience = ({id}) => {
  return (
    <Section id={id}>
        <SectionExperienceContainer>
          <SectionExperienceTitle>{id.toUpperCase()}</SectionExperienceTitle>
        </SectionExperienceContainer>
        <Timeline items={experienceList}></Timeline>
      {/* {ExperienceCardList} */}
    </Section>
  )
}


export default SectionExperience;