import React from "react";
import styled from 'styled-components';
import SectionIntro from './SectionIntro';
import SectionExperience from './SectionExperience';
import SectionProject from './SectionProject';
import SectionSkill from './SectionSkill';
import Divider from './Divider';

const SectionListContainer = styled.ul`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
`;

const SectionList = () => {
  return (
    <SectionListContainer>
      <SectionIntro id={'hi'}></SectionIntro>
      <Divider></Divider>
      <SectionExperience id={'experiences'}></SectionExperience>
      <Divider></Divider>
      <SectionProject id={'projects'}></SectionProject>
      <Divider></Divider>
      <SectionSkill id={'skills'}></SectionSkill>
    </SectionListContainer>
  )
}


export default SectionList;