import React from "react";
import styled from 'styled-components';
import Section from './Section';
import Card from './Card';
import skillData from '../../data/skillData.json';

const skillList = skillData.skillList

const SectionSkillContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 64px;
  padding: 0 14% 0;
  align-items: center;
`;

const SectionSkillTitle = styled.h2`
  font-size: 3em;
`;

const StyledCard = styled(Card)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding 32px 16px;
  margin: 16px 0px;
  box-sizing: border-box;
  text-align: center;
  min-width: 220px;
`;

const SkillCardListContainer = styled.div`
  display: grid;
  width: 100%;
  grid-row-gap: 32px;
  grid-column-gap: 32px;
  grid-auto-rows: minmax(200px, 1fr);
  grid-template-columns: repeat(4, minmax(220px, 1fr));
  
  @media (max-width: 1400px) {
    grid-template-columns: repeat(2, minmax(220px, 1fr));
  }
`

const SkillCardList = skillList.map((skill, index) => {
  const { title, content } = skill;
  return (
    <StyledCard key={index} title={title} content={content}></StyledCard>
  )
})

const SectionSkill = ({id}) => {
  return (
    <Section id={id}>
      <SectionSkillContainer>
        <SectionSkillTitle>{id.toUpperCase()}</SectionSkillTitle>
        <SkillCardListContainer>{SkillCardList}</SkillCardListContainer>
      </SectionSkillContainer>
    </Section>
  )
}


export default SectionSkill;
