import React from "react";
import styled from 'styled-components';
import { GithubIcon, LinkedinIcon } from "../Icons";
const SocialButtonContainer = styled.a`
  font-size: 0;
  width: 60px;
  padding: 5px;
  border: 2px solid var(--space-cadet-color-white);
  border-radius: 20%;
  box-sizing: border-box;
  margin: 0 16px;
  transition: all 150ms ease-in;

  &:hover {
    border-color: var(--material-color-light-blue-700);
    svg {
      fill: var(--material-color-light-blue-700);
    }
  }

  & svg {
    transition: all 150ms ease-in;
    width: 100%;
    height: 100%;
    fill: var(--space-cadet-color-white);
  }
`;

const GithubButton = ({url}) => {
  return (
    <SocialButtonContainer href={url} target="_blank" rel="noopener" title="github">
      <GithubIcon></GithubIcon>
    </SocialButtonContainer>
  )
}

const LinkedinButton = ({url}) => {
  return (
    <SocialButtonContainer href={url} target="_blank" rel="noopener" title="linkedin">
      <LinkedinIcon></LinkedinIcon>
    </SocialButtonContainer>
  )
}

export {
  GithubButton,
  LinkedinButton
}
