import React from "react";
import styled from 'styled-components';
import NavItem from './NavItem';

const NavRailDiv = styled.ul`
  display: flex;
  align-items: center;
  list-style-type: none;
  margin: 0px;
  padding: 0px;
`;

const sectionList = ['hi', 'experiences', 'projects', 'skills'];
const navItems = sectionList.map((id, index) => {
  return (
    <NavItem key={index} toId={id}>{id.toUpperCase()}</NavItem>
  )
})
const NavRail = () => {
  return (
    <NavRailDiv>
      {navItems}
    </NavRailDiv>
  )
}


export default NavRail;