import React from "react";
import styled from 'styled-components';
import Section from './Section';
import SocialLinks from '../SocialLinks/SocialLinks'

const SectionIntroContainer = styled.div`
  display: flex;
  margin-top: 64px;
  padding: 0 14% 0;
`;

const SectionIntroDescription = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const SectionIntroTitle = styled.h2`
  font-size: 3em;
`;

const SectionIntroContent = styled.p`
  font-size: 1.2em;
  line-height: 1.5em;
  margin: 8px 0 0;
`;

const ProfileContainer = styled.div`
  margin-left: 96px;
  width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.5em;
  padding: 24px;
  box-sizing: border-box;
  flex-shrink: 0;
`;

const ProfileImgContainer = styled.div`
  width: 62%;
`;

const ProfileImg = styled.img`
  width: 100%;
  border-radius: 50%;
  object-fit: cover;
`;

const ProfileDescription = styled.div`
  margin-top: 16px;
`;

const personalIntro = (
  <SectionIntroDescription>
    <SectionIntroTitle>Hi!</SectionIntroTitle>
    <SectionIntroContent>My name is Chia-Hao Chang,</SectionIntroContent>
    <SectionIntroContent>Working as a Front-End Web Developer for Yahoo!</SectionIntroContent>
    <SectionIntroContent>I am Passionate about challenging the fresh and learning new programming skills.</SectionIntroContent>
  </SectionIntroDescription>
)

const personalProfile = (
  <ProfileContainer>
    <ProfileImgContainer>
      <ProfileImg alt='profile' src={process.env.PUBLIC_URL + '/profilePicture.jpg'}></ProfileImg>
    </ProfileImgContainer>
    <ProfileDescription>Chia-Hao</ProfileDescription>
    <ProfileDescription>Software Engineer</ProfileDescription>
    <SocialLinks></SocialLinks>
  </ProfileContainer>
)

const SectionIntro = ({id}) => {
  return (
    <Section id={id}>
      <SectionIntroContainer>
        {personalIntro}
        {personalProfile}
      </SectionIntroContainer>
    </Section>
  )
}


export default SectionIntro;