import React, { useState, useEffect, useRef, useCallback } from 'react';
import styled from 'styled-components';
import _ from 'lodash';
import TimeList from './TimeList';

const TimelineContainer = styled.div`
  width: 100%;
  height: 600px;
  box-sizing: border-box;

  background: var(--material-color-grey-800);
  & * {
    user-select: none;
  }
`;

const TimelineListContaniner = styled.div`
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
`;
// width: ${({nums}) => nums ? `${nums*25}%` : '0px'};
const ITEM_WIDTH = 400;
const Timeline = ({items}) => {
  const isMouseDown = useRef(false);
  const initPageX = useRef(0);
  const initOffsetX = useRef(0);
  const currentOffsetX = useRef(0);
  const containerRef = useRef(null);
  const [offsetX, setOffsetX] = useState(0);
  const ITEM_NUM = [...items].length - 1;

  const focusOn = useCallback((targetId) => {
    const width = containerRef.current.getBoundingClientRect().width;
    const centerOffset = width / 2;    
    if (Number.isInteger(targetId)) {
      currentOffsetX.current = centerOffset - (ITEM_WIDTH * targetId) - (ITEM_WIDTH / 2);
    } else {
      let focusId = Math.floor((centerOffset - currentOffsetX.current) / ITEM_WIDTH);
      focusId = Math.min(focusId, ITEM_NUM);
      focusId = Math.max(focusId, 0);
      currentOffsetX.current = centerOffset - (ITEM_WIDTH * focusId) - (ITEM_WIDTH / 2);
    }
    setOffsetX(currentOffsetX.current);
  }, [containerRef, ITEM_NUM])

  useEffect(() => {
    if (ITEM_NUM > 1) {
      focusOn(ITEM_NUM - 1);
    } else {
      focusOn(ITEM_NUM);
    }
  }, [focusOn, ITEM_NUM]);

  const updateDragging = _.throttle((pageX) => {
    const scrollOffsetX =  pageX - initPageX.current;
    currentOffsetX.current = initOffsetX.current + scrollOffsetX;
    setOffsetX(currentOffsetX.current);
  }, 15);

  const handleMouseDown = (event) => {
    if(!isMouseDown.current) {
      containerRef.current.style.removeProperty('transition');
      const { pageX } = event;
      isMouseDown.current = true;
      initPageX.current = pageX;
      initOffsetX.current = currentOffsetX.current;
      updateDragging(pageX);
    }
  }
  
  const handleMouseUp = (event) => {
    if(isMouseDown.current) {      
      containerRef.current.style.transition = '0.25s';
      isMouseDown.current = false;
      focusOn();
    }
  }
  
  const handleMouseMove = (event) => {
    if(isMouseDown.current) {
      const { pageX } = event;
      updateDragging(pageX);
    }
  }

  return (
    <TimelineContainer
      onMouseDown={handleMouseDown}
      onMouseUp={handleMouseUp}
      onMouseMove={handleMouseMove}
      onMouseLeave={handleMouseUp}
    >
      <TimelineListContaniner 
        ref={containerRef}
        style={{transform: `translate(${offsetX}px, 0)`}}
      >
        <TimeList
          items={items}
        >
        </TimeList>
      </TimelineListContaniner>
    </TimelineContainer>
  )
}

export default Timeline;


