import React, { forwardRef } from "react";
import styled from 'styled-components';
import TimeItem from "./TimeItem";

const TimeListContianer = styled.ul`
  height: 100%;
  list-style-type: none;
  display: flex;
  padding: 0;
`;

const TimeList = ({items}, ref) => {
  const itemsNum = [...items].length;
  const TimeItems = [...items].map((item, index) => {
    return (
      <TimeItem key={index} {...item} order={index+1} nums={itemsNum}></TimeItem>
    )
  });

  return (
    <TimeListContianer ref={ref}>
      {TimeItems}
    </TimeListContianer>
  )
}


export default forwardRef(TimeList);