import React from "react";
import styled from 'styled-components';

const YearTitle = styled.h4`
  // margin: 0 0 16px;  
  ${({rgbColor}) => `color: ${rgbColor};`}
  font-size: 1.5em;
  z-index: 1;
`;

const YearFocus = styled.div`
  content: "";
  position: absolute;
  box-sizing: border-box;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  border: 5px solid var(--space-cadet-color-white);
  ${({rgbColor}) => `border: 12px solid ${rgbColor};`}
  background: var(--space-cadet-color-white);
`;

const YearLine = styled.div`
  content: "";
  position: absolute;
  box-sizing: border-box;
  ${({rgbColor}) => `background ${rgbColor};`}
  width: 12px;
  height: 50%;
  ${({reverse}) => `${reverse ? 'top: 0' : 'bottom: 0'};`}
`;

const TimeItemTitle = styled.h4`
  margin: 0 0 16px;
  font-size: 1.5em;
`;

const TimeItemContent = styled.p`
  margin: 0;
  font-size: 1em;
`;

const TimeLineContainer = styled.div`
  width: 104%;
  box-sizing: border-box;
  ${({rgbColor}) => `border: 16px solid ${rgbColor};`}
  ${({rgbColor}) => `border-right: 16px solid ${rgbColor};`}
  transform: translate(-2%, 0);
  left: 0%;
  border-left: 16px solid transparent;

  ::before {
    content: "";
    transform: translate(-50%,-50%);
    ${({rgbColor}) => `border: 10px solid ${rgbColor};`}
    position: absolute;
    box-sizing: border-box;
    border-radius: 50%;
    width: 62px;
    height: 62px;
    background: var(--space-cadet-color-white);
    left: 50%;
  }
`;


const TimeContainer = styled.div`
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 45%;
  padding: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const DescriptionContainer = styled.div`
  box-sizing: border-box;
  width: 100%;
  height: 45%;
  padding: 48px 24px;
`;

const TimeItemContianer = styled.li`
  padding: 0px;
  box-sizing: border-box;
  // background: var(--material-color-light-blue-800);
  // box-shadow: 0 2px 4px var(--custom-black);
  // rgba(var(--custom-black), 0.9)
  
  height: 100%;
  flex-shrink: 0;
  display: flex;
  ${({reverse}) => `flex-direction: ${reverse ? 'column-reverse' : 'column'};`}
  
  justify-content: center;
  width: 400px;

  &:last-of-type {
    ${TimeLineContainer} {
      ::after {
        content: "";
        position: absolute;
        box-sizing: border-box;
        border-radius: 50%;
        ${({rgbColor}) => `border: 24px solid ${rgbColor};`}
        transform: translate(50%,-50%);
        right: 0;
      }
    }
  }
`;
  
const baseGreen = 16;
const baseBlue = 56;
const fullGreen = 72;
const fullBlue = 95;
const TimeItem = ({ title, content, year,  className, order, nums }) => {
  const redColorPercent = 0;
  const greenColorPercent = (fullGreen-baseGreen)/nums * order + baseGreen;
  const blueColorPercent = (fullBlue-baseBlue)/nums * order+ baseBlue;
  const rgbColor = `rgb(${redColorPercent}%, ${greenColorPercent}%, ${blueColorPercent}%)`;

  return (
    <TimeItemContianer className={className} reverse={!!(order%2)} rgbColor={rgbColor}>
      <TimeContainer>
        <YearTitle rgbColor={rgbColor}>
          {year}
        </YearTitle>
        <YearLine rgbColor={rgbColor} reverse={!!(order%2)}></YearLine>
        <YearFocus rgbColor={rgbColor}></YearFocus>
      </TimeContainer>
      <TimeLineContainer 
        rgbColor={rgbColor}
      >
      </TimeLineContainer>
      <DescriptionContainer>
        <TimeItemTitle>{title}</TimeItemTitle>
        <TimeItemContent>{content}</TimeItemContent>
      </DescriptionContainer>
    </TimeItemContianer>
  )
}


export default TimeItem;


