import React from "react";
import styled from 'styled-components';

const FooterDiv = styled.div`
  display: flex;
  width: 100%;
  height: 50%;
  max-height: 50%;
  box-sizing: border-box;
  background: var(--material-color-grey-900);
`;

const Footer = ({...props}) => {
  return (
    <FooterDiv>
    </FooterDiv>
  )
}


export default Footer;