import React from "react";
import styled from 'styled-components';

const LogoContainer = styled.div`
  display: flex;
  align-items: center;
  flex-shrink: 0;
  &:hover * {
    color: var(--material-color-light-blue-500);
  }
`;

const Logo  = styled.a`
  display: inline-block;
  font-size: 1.8em;
  line-height: 1;
  text-decoration: none;
  transition: color 150ms ease-in;
`;

const HeaderLogo = () => {
  return (
    <LogoContainer>
      <Logo href={'/'}>CHIA-HAO</Logo>
    </LogoContainer>    
  )
}


export default HeaderLogo;